import PressBackTop from 'press-ui/press-back-top/press-back-top.vue';
import PressCheckbox from 'press-ui/press-checkbox/press-checkbox.vue';
import PressNoticeBar from 'press-ui/press-notice-bar/press-notice-bar';

let that;
export default {
  i18n: {
    'zh-CN': {
      square: '显示方形',
      icon: '自定义图标',
      distance: '自定义距离',
      style: '自定义样式',
      text: '是否显示文本',
    },
    'en-US': {
      square: 'Square',
      icon: 'Custom Icon',
      distance: 'Custom Distance',
      style: 'Custom Style',
      text: 'Custom Text',
    },
  },
  components: {
    PressBackTop,
    PressCheckbox,
    PressNoticeBar,
  },
  data() {
    that = this;

    return {
      scrollTop: 0,
      newScrollTop: 0,
      valueMap: {
        square: false,
        icon: false,
        distance: false,
        style: false,
        text: false,
      },
    };
  },
  computed: {
    backTopProps() {
      const { square, icon, distance, style, text } = this.valueMap;
      return {
        mode: square ? 'square' : 'circle',
        icon: icon ? 'arrow-up' : 'back-top',
        top: distance ? 10 : 100,
        iconStyle: style ? 'color: #fff' : '',
        customStyle: style ? 'background-color: #2979ff' : '',
        text: text ? '顶部' : '',
      };
    },
  },
  methods: {
    onChange(value, key) {
      this.valueMap[key] = value;
    },
    onScroll(e) {
      this.scrollTop = e.target.scrollTop ?? e.detail.scrollTop;
      console.log('[scrollTop]', e, this.scrollTop);
    },
    scrollToTop() {
      that.newScrollTop = that.scrollTop;
      setTimeout(() => {
        that.newScrollTop = 0;
      });
    },
  },
};
